export const normalizeNumber = (number) => {
    switch (true) {
        case (number > 999999):
            number = `${(number/1000000).toFixed(2)}t`
            break;
        case (number > 99999):
            number = `${(number/1000).toFixed(0)}k`
            break;
        case (number > 9999):
            number = `${(number/1000).toFixed(1)}k`
            break;
        case (number > 999):
            number = `${(number/1000).toFixed(2)}k`
            break;
        default:
            number = `${number}`
            break;
    }
    return number
}